body{
    scroll-behavior: smooth;
}

.main{
    height: 100vh;
    width: 100vw;
    padding-top: 5vw ;
    scroll-snap-type: x mandatory ;
}


.info-wrapper{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 5vw;

}

.vector{
  height: 35vw;
  width: 35vw;
}

.signature {
    position: absolute  ;
    justify-content: center;
    align-items: center;
    fill: transparent;
    animation: draw 2s linear ;
    height: 25vw;
    width: 25vw;
    stroke-width:0.2vw;
    
}



@keyframes draw {
    0% {
        stroke-dasharray: 0 311;
        stroke-dashoffset: 0 311;
      }
      100% {
        stroke-dasharray: 311 0;
        stroke-dashoffset: 311 0;
      }
