.mango-vector{
    margin-top: 4vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.big-text{
    font-size: 5vw;
    color: whitesmoke;
    font-family: 'Poppins', sans-serif;
    text-align: right;
    margin: 0;
    font-weight: 700;
    text-shadow: #ac6060 0.15vw 0.15vw;
}

.normal-text{
    font-size: 2vw;
    color: whitesmoke;
    font-family: 'Poppins', sans-serif;
    text-align: right;
    margin: 0;

}

a{
    text-decoration: none;
}

.small-text{
    color: whitesmoke;
    font-family: 'Poppins', sans-serif;
    font-size: 1vw;
    text-align: center;
    animation: up-down 1s ;
    animation-iteration-count: infinite;
    text-shadow: #242323 0.1vw 0.1vw;

}

.screen-widget{
    flex: 0 0 100%;
    scroll-snap-align: center;
}


@keyframes up-down {
    0%{
        transform: translateY(-20%);
    }

    50%{
        transform: translateY(20%);
    }
    100%{
        transform: translateY(-20%);
    }
}

@media screen and (max-width:800px) {

.main{
  height: 100vh;
  width: 100vw;
  padding-top: 19vw ;
  scroll-snap-type: x mandatory ;
}

.info-wrapper{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 5vw;
flex-direction: column;
/* background: red; */
}

.vector{
  height: 55vw;
  width: 55vw;
}

.signature {
    position: absolute  ;
    justify-content: center;
    align-items: center;
    fill: white;
    /* stroke-dasharray: 0;
    stroke-dashoffset: 0; */
    animation: draw 15s linear ;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    height: 45vw;
    width: 45vw;
    stroke-width:0.4vw;
    
}

.big-text{
  font-size: 10vw;
  color: whitesmoke;
  font-family: 'Poppins', sans-serif;
  text-align: center;
  margin: 5vw 0;
  font-weight: 700;
  text-shadow: #ac6060 0.15vw 0.15vw;
  text-decoration: underline;
  text-decoration-style:wavy ;
  text-decoration-thickness: 2px;
}



.normal-text{
  font-size: 5vw;
  color: whitesmoke;
  font-family: 'Poppins', sans-serif;
  text-align: center;
 
}

.small-text{
  color: whitesmoke;
  font-family: 'Poppins', sans-serif;
  font-size: 3vw;
  text-align: center;
  animation: up-down 1s ;
  animation-iteration-count: infinite;
  text-shadow: #242323 0.1vw 0.1vw;
  margin-top: 2vw;
  
}
}